body {
  min-width: 320px;
  margin: 0;
  font-family: 'Roboto', sans-serif;
  color: #191919;
}
* {
  box-sizing: border-box;
}
*:before,
*:after {
  box-sizing: border-box;
}
html {
  font-size: 12px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
body {
  text-transform: uppercase;
}
input,
button,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}
img {
  max-width: 100%;
}
h1,
h2,
h3 {
  font-weight: 400;
}
.line-break {
  display: block;
}
@media (min-width: 768px) {
  .line-break {
    display: inline-block;
  }
}
header {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}
.header-inner {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 30px 0 ;
  justify-content: space-between;
  width: 250px;
  margin: 0 auto;
}
@media (min-width: 768px) {
