    justify-content: start;
  }
}
.gamburger {
  position: relative;
  bottom: 3px;
  left: 0;
  display: block;
  cursor: pointer;
}
.gamburger span {
  position: relative;
  left: 4px;
  width: 20px;
  height: 1.5px;
  background-color: #767676;
  display: inline-block;
  cursor: pointer;
}
@media (min-width: 768px) {
  .gamburger span {
    display: none;
  }
}
.gamburger span:before {
  content: "";
  cursor: pointer;
  position: absolute;
  top: 4px;
  right: 0px;
  width: 20px;
  height: 1.5px;
  background-color: #767676;
}
.gamburger span:after {
  content: "";
  position: absolute;
  cursor: pointer;
  top: 8px;
  right: 0px;
  width: 20px;
  height: 1.4px;
  background-color: #767676;
}
